import * as phina from "phina.js";

phina.globalize();

// 開始位置指定の直線クラス
export default phina.define('Triangle', {
  superClass: 'phina.display.Shape',
  init: function(baseDropSize, offsetY, strokeColor, fillColor) {
    this.superInit({
      width: baseDropSize,
      height: baseDropSize,
      backgroundColor: 'transparent',
    });
    phina.display.TriangleShape({
      fill: fillColor,
      stroke: strokeColor,
      strokeWidth: 1,
      radius: 2,
    })
    .moveTo(0, offsetY)
    .addChildTo(this);
  },
  setAlpha: function(alpha) {
    this.children[0].alpha = alpha;
  },
});