<template>
  <button
    :class="{active: active}"
    @click="commonProcessing"
    @click.right.stop.prevent="commonProcessing"
  ><slot></slot></button>
</template>

<script>
export default {
  name: 'SkillButton',
  props: {
    skill: Object,
  },
  data: function() {
    return {
      active: false,
    }
  },
  methods: {
    // 共通の処理
    commonProcessing: function(e) {
      //this.$root.focus();
      if (this.active) {
        return;
      } else {
        this.activate();
      }
      if (this.skill.hasOwnProperty('common')) {
        this.skill.common.bind(this)(e);
      }
      switch (e.type) {
        case 'click'        : this.clickLeftProcessing(e); break;
        case 'contextmenu'  : this.clickRightProcessing(e); break;
      }
    },
    // 左クリック時の処理
    clickLeftProcessing: function(e) {
      this.skill.left.bind(this)(e);
    },
    // 右クリック時の処理
    clickRightProcessing: function(e) {
      if (this.skill.hasOwnProperty('right')) {
        this.skill.right.bind(this)(e);
      } else {
        this.skill.left.bind(this)(e);
      }
    },
    // クリックエフェクト用のclassを付与する
    activate: function(){
      this.active = true;
      this.$playSound("sound-button");
      setTimeout(this.deactivate, 500);
    },
    // クリックエフェクト用のclassを解除する
    deactivate: function(){
      this.active = false;
    },
  },
}
</script>

<style scoped>
button {
  min-width: 70px;
  min-height: 30px;
  font-size: 14px;
  color: rgb(23,210,207);
  background: rgb(10,19,28);
  border: 2px solid rgb(23,210,207);
  border-radius: 2px;
  cursor: pointer;
}

button.active {
  animation-name: active;
  animation-duration: 0.25s;
  animation-iteration-count: 2;
  animation-direction: alternate;
  animation-timing-function: linear;
}

@keyframes active {
  100% {
    background: rgb(141, 233, 222);
    color: rgb(25, 54, 72);
  }
}
</style>
