<template>
  <label v-on:wheel="changeNumber">
    {{ upperCamelCaseLabel }}
    <input
      type="number"
      :min="setting.min"
      :max="max"
      :step="setting.step"
      v-show="false"
      v-model.number="setting.value"
    />
    <span class="checked">{{ numberToDisplay }}</span>
  </label>
</template>

<script>
import mixin from '../../../mixins/UpperCamelCase'
import {TweenLite} from 'gsap'

export default {
  name: 'SearchSetting',
  props: {
    label: String,
    setting: Object,
    maxCombo: Number,
  },
  mixins: [mixin],
  data: function() {
    return {
      tweenedNumber: this.setting.value,
    }
  },
  computed: {
    // コンボ指定のみ最大コンボ数をmaxとする
    max: function() {
      return this.setting.hasOwnProperty('max') ? this.setting.max : this.maxCombo;
    },
    // ビーム幅のみアニメーションをつける
    numberToDisplay: function(){
      return (this.setting.step === 1) ? this.setting.value : this.animatedNumber;
    },
    animatedNumber: function(){
      return this.tweenedNumber.toFixed(0);
    },
  },
  watch: {
    "setting.value": function(newValue) {
      TweenLite.to(this.$data, 0.25, {tweenedNumber: newValue})
    },
  },
  methods: {
    // ホイール時の処理
    changeNumber: function(){
      const step = parseFloat(this.setting.step),
            min = parseFloat(this.setting.min),
            max = parseFloat(this.max);

      if (event.deltaY < 0) {
        if (this.setting.value + step <= max) this.setting.value += step;
      } else {
        if (this.setting.value - step >= min) this.setting.value -= step;
      }
    }
  },
}
</script>

<style lang="scss" scoped>
label {
  padding: 5px;
  font-size: 13px;

  span {
    width: 17px;
    display: inline-block;
    text-align: center;
  }
}
</style>