<template>
  <fieldset id="search-data">
    <table v-if="stateFlag === this.$STATE.SEARCH_END">
      <tr><th>TIME</th><td>{{ elapsedTime }} sec</td></tr>
      <tr><th>MOVE</th><td>{{ movedCount }}</td></tr>
      <tr><th>STATUS</th><td :class="statusClass">{{ searchStatus }}</td></tr>
    </table>
  </fieldset>
</template>

<script>
export default {
  name: 'SearchData',
  props: {
    stateFlag: Number,
    bestNode: Object,
    searchStatus: String,
  },
  computed: {
    // 探索に要した時間
    elapsedTime: function(){
      return this.bestNode ? this.bestNode.elapsedTime : 0;
    },
    // 移動手数
    movedCount: function() {
      return this.bestNode ? this.bestNode.movedCount : 0;
    },
    // STATUSに付与するclass
    statusClass: function(){
      return {
        ok    : (this.searchStatus === "OK"),
        danger: (this.searchStatus === "DANGER"),
      };
    },
  }
}
</script>

<style scoped>
#search-data {
  height: 92px;
  margin-left: 7px;
}

#search-data table {
  height: 70px;
  margin: 0 auto;
}

#search-data th,
#search-data td {
  font-size: 0.95em;
  line-height: 1.2em;
  vertical-align: bottom;
}

#search-data th {
  font-weight: normal;
  text-align: left;
}

#search-data td {
  padding-left: 10px;
}

.ok {
  color: white;
  text-shadow: 0 0 2px #07f7e7;
  text-shadow: 0 0 2px blue;
}

.danger {
  color: white;
  font-weight: bold;
  text-shadow: 0 0 3px red;
  animation-name: danger;
  animation-duration: 0.25s;
  animation-iteration-count: infinite;
  animation-timing-function: linear;
  animation-direction: alternate;
}

@keyframes danger {
  100% {
    color: red;
  }
}
</style>
