import * as phina from "phina.js"
import defineMainScene from "./scene/main"
import defineInputScene from "./scene/input"
import MyGameApp from "./myGameApp"

// Arrayに配列要素をスワップするメソッドを追加
Array.prototype.swap = function(idx_1, idx_2) {
  [this[idx_1], this[idx_2]] =
  [this[idx_2], this[idx_1]];
}

// ゲームを作成する関数
// 戻り値：Promise
// (成功時：ゲームインスタンスをresolve),
function createGame (options) {
  return new Promise(resolve => {
    defineMainScene();
    defineInputScene();

    phina.main(function() {
      const app = MyGameApp({
        query: '#game-canvas',
        fit: false,
        gameData: options,
      });
      app.run();
      resolve(app);
    });
  });
}

export default createGame;