<template>
  <tr>
    <th :class="{ checked: !data[0] }">{{ upperCamelCaseLabel }}</th>
    <td>
      <label v-for="(bool, index) in data" :key="index" :class="{ checked: bool && index }">
        <input
          type="checkbox"
          v-show="false"
          v-model="data[index]"
          :disabled="bool && !index"
          @change="changeCheckbox(index)"
        ><img v-if="index" :src="getImgSrc(index)" class="drop"><!--
      --><span v-else>NONE</span><!--\
    --></label>
    </td>
  </tr>
</template>

<script>
import mixin from "../../../mixins/UpperCamelCase"

export default {
  name: 'ClearingSetting',
  props: {
    data: Array,
    label: String,
  },
  mixins: [mixin],
  methods: {
    // チェックボックスを変更したときの処理
    changeCheckbox: function(clickedIdx) {
      if (clickedIdx) {
        this.data.splice(0, 1, !this.data.slice(1).find(elm => elm));
      } else {
        setTimeout(() => {
          this.data.forEach((item, idx) => {
            this.data.splice(idx, 1, !idx);
          });
        }, 0);
      }
      if (clickedIdx && this.data[clickedIdx]) {
        this.$playSound("sound-on");
      } else {
        this.$playSound("sound-off");
      }
    },
    // imgのsrcを得るメソッド(特殊な例外に対応するために必要)
    getImgSrc: function(index) {
      if (this.label === "line" && index === 7) {
        return require(`../../../assets/img/drop_6_2.png`);
      } else {
        return require(`../../../assets/img/drop_${index}.png`);
      }
    },
  },
}
</script>

<style scope>
#clearing-settings table td,
#clearing-settings table th {
  padding : 0;
}

#clearing-settings table th:first-child {
  padding: 0 10px 0 5px;
  font-size: 12px;
}

#clearing-settings td label {
  font-size: 12px;
}

#clearing-settings table label {
  margin: 0px 3px;
  padding: 1px 3px;
  display: inline-block;
  line-height: 21px;
  vertical-align: middle;
  cursor: pointer;
}

#clearing-settings .drop {
  width: 21px;
  opacity: 0.2;
  vertical-align: middle;
  position: relative;
  top: -1px;
}

#clearing-settings .checked .drop {
  opacity: 1;
  box-sizing: border-box;
}

.checked {
  color: orange;
}

th:not(.checked),
label:not(.checked),
label:not(.checked) img {
  transition: 0.2s all;
}
</style>
