import * as phina from "phina.js"

phina.globalize();

// ボタンクラス
// 必須プロパティ：text
export default phina.define('StylishButton', {
  superClass: 'Button',

  init: function(text) {
    const options = {
      'width': 117,
      'height': 117,
      'text': text,
      'fill': 'rgba(0, 0, 0, 0.1)',
      'stroke': 'rgb(0,227,255)',
      'fontColor': 'rgb(0,227,255)',
      'fontSize': 22,
    };
    this.superInit(options);
  },
});
