import * as phina from "phina.js"
import BaseScene from "./base"
import StylishButton from "../ui/stylishButton"
import { NormalDrop, GrabbedDrop } from "../display/drops"
import Dragon from "../display/dragon/dragon"
import MoveCountLabel from "../display/moveCountLabel"

export default function() {
  // Main Scene
  phina.define('MainScene', {
    superClass: BaseScene,
    init: function(options) {
      this.superInit(options);
      this._initFlags();
      this._setButtons();
      this._addEventToButtons();
      this._setDrops();
      this._setMoveCountLabel();
      this.initImmovablePositionSprites();
      this.initStartPositionSprite();

      if (options.process) {
        this.summonDragon(options.process);
        if (options.moveFlag) {
          setTimeout(() => {
            this._moveDrops(options.moveTime);
          }, options.moveDuration);
        }
      }
    },

    // 各種フラグ、パラメータを初期化するメソッド
    _initFlags: function(){
      this.dragOkFlag = true;   // ドロップを動かしていいかどうか
      this.changeFlag = false;  // ドロップを動かしたかどうか
      this.lineFlag = true;     // 手順線を表示するかどうか
    },

    // ボタンを配置するメソッド
    _setButtons: function() {
      const buttonTexts = ['INPUT', 'RESET', 'SHUFFLE', 'SEARCH', 'MOVE', 'DRAGON'],
            GridX = phina.util.Grid(this.screenPixelWidth, buttonTexts.length);

      buttonTexts.forEach((text, index) => {
        const x = GridX.span(index) - 4;
        StylishButton(text).setOrigin(0, 0).moveTo(x, 0).addChildTo(this.buttonGroup);
      });
    },

    // ボタンにイベントを設定するメソッド
    _addEventToButtons: function() {
      // INPUTボタンのイベント
      this.buttonGroup.children[0].addEventListener('pointstart', () => {
        this._exitTo('input');
      });
      // RESETボタンのイベント
      this.buttonGroup.children[1].addEventListener('pointstart', () => {
        this._exitTo('main');
      });
      // SHUFFLEボタンのイベント
      this.buttonGroup.children[2].addEventListener('pointstart', () => {
        this.vueComponent.shuffle();
      });
      // SEARCHボタンのイベント
      this.buttonGroup.children[3].addEventListener('pointstart', () => {
        this.vueComponent.search();
      });
      // MOVEボタンのイベント
      this.buttonGroup.children[4].addEventListener('pointstart', () => {
        this.moveDrops(120);
      });
      // LINEボタンのイベント
      this.buttonGroup.children[5].addEventListener('pointstart', () => {
        if (this.dragon) {
          if (this.lineFlag) {
            this.dragon.fadeOut(200);
          } else {
            this.dragon.fadeIn(200);
          }
          this.lineFlag = !this.lineFlag;
        }
      });
    },

    // ドロップを配置するメソッド
    _setDrops: function() {
      this.board.forEach((color, index) => {
        const [x, y] = this.getCoordinatesOfDrop(index);
        NormalDrop(this.baseDropSize).setFrameIndex(color)
          .setScale(this.dropScale).moveTo(x, y).addChildTo(this.dropGroup);
      })
    },

    // 移動回数ラベルを配置するメソッド
    _setMoveCountLabel: function() {
      const x = this.screenPixelWidth - 65,
            y = this.topMargin - 185;
      this.moveCountLabel = MoveCountLabel().moveTo(x, y).addChildTo(this);
    },

    // シーンを移動するメソッド
    _exitTo: function(label) {
      this.exit(label, {
        boardData: this.boardData,
        screenData: this.screenData,
        process: this.process,
      });
    },

    // ドロップの操作を開始したときのイベント処理
    onpointstart: function(e) {
      if (!this.dragOkFlag) return;

      const [X, Y, Z] = this.getPositionOfDrop(e);
      if (this.isOutOfBoard(X, Y)) return;

      const color = this.board[Z];
      this.dragStartIndex = Z;
      this.dragStartDrop = this.dropGroup.children[Z].setAlpha(0.3);
      this.grabbedDrop = GrabbedDrop(this.baseDropSize).setFrameIndex(color)
                          .setScale(this.dropScale * 1.1).setAlpha(0.7).move(e).addChildTo(this);
      if (this.dragon && this.lineFlag) {
        this.dragon.setAlpha(0.7, 300);
      }
    },

    // ドロップを動かしているときのイベント処理
    onpointmove: function(e) {
      if (!this.dragOkFlag) return;

      const [X, Y, Z] = this.getPositionOfDrop(e);
      if (this.isOutOfBoard(X, Y)) return;

      this.grabbedDrop.move(e);

      if (this.dragStartIndex !== Z) {
        this._swapDrops(this.dragStartIndex, Z, true);
        this.changeFlag = true;
        this.dragStartIndex = Z;
        this.moveCountLabel.increment();
      }
    },

    // ドロップの操作を終了したときのイベント処理
    onpointend: function() {
      if (!this.dragOkFlag) return;

      if (this.dragStartDrop) {
        this.dragStartDrop.setAlpha(1);
        this.grabbedDrop.remove();
        delete this.dragStartDrop;
        delete this.grabbedDrop;
      }
      if (this.changeFlag) {      
        this._finishMoveAnimation();
        this._clearDrops();
        if (this.dragon) {
          this.dragon.hide();
        }
      } else {
        if (this.dragon && this.lineFlag) {
          this.dragon.fadeIn(300);
        }
      }
    },

    // ドロップを入れ替えるメソッド
    // 引数：moveFlag(アニメーションでの移動をさせるかどうか)
    _swapDrops: function(index_1, index_2, moveFlag = false) {
      const drops = this.dropGroup.children;

      if (moveFlag) {
        const [x1, y1] = this.getCoordinatesOfDrop(index_1),
              [x2, y2] = this.getCoordinatesOfDrop(index_2);
        this._finishMoveAnimation();
        drops[index_1].tweenMoveTo(x2, y2, 50);
        drops[index_2].tweenMoveTo(x1, y1, 50);
      }
      drops.swap(index_1, index_2);
      this.board.swap(index_1, index_2);
    },

    // 全てのドロップの移動を完了させるメソッド
    _finishMoveAnimation: function() {
      this.dropGroup.children.forEach(drop => drop.tweenMoveTo());
    },

    // ドロップを消すメソッド
    _clearDrops: function() {
      const FADE_TIME = 250,      // 一つのコンボが消えるのにかかる時間（ミリ秒）
            COMBO_INTERVAL = 250, // コンボ同士の間隔（ミリ秒）
            CALL_INTERVAL = 50,   // 全てのコンボが消えてから、落とすメソッドを呼ぶまでの間隔（ミリ秒）
            clearablePlaces = this._getClearablePlacesAsArray(this.board);

      // 消えるドロップがなければ終了
      if (!clearablePlaces.length) return;

      this.dragOkFlag = false;
      for (let i = 0; i < clearablePlaces.length; i++) {
        for (let j = 0; j < clearablePlaces[i].length; j++) {
          const index = clearablePlaces[i][j],
                drop = this.dropGroup.children[index],
                delayTime = (FADE_TIME + COMBO_INTERVAL) * i;

          this.board[index] = 0;
          drop.tweener.wait(delayTime).fadeOut(FADE_TIME).play();
        }
      }
      // コンボ数と、全てのドロップが消えるまでにかかる時間
      const combo = clearablePlaces.length,
            totalTime = (FADE_TIME * combo) + (COMBO_INTERVAL * (combo - 1)) + CALL_INTERVAL;

      // 全てのドロップが消えるのを待ってから、ドロップを落とすメソッドを呼び出す
      this.tweener.wait(totalTime).call(this._dropDrops.bind(this)).play();
    },

    // ドロップを落とすメソッド
    _dropDrops: function() {
      const FALL_TIME = 270,      // ドロップが落ちるまでにかかる時間(ミリ秒)
            CALL_INTERVAL = 80;   // ドロップが落ちてから、removeDropsメソッドを呼ぶまでの間隔

      for (let X = 0; X < this.boardWidth; X++) {
        for (let Y = this.boardHeight - 1; 0 < Y; Y--) {
          let low = this.get1dIndex(X, Y);
          if (this.board[low] === 0) {
            for (let i = 1; i <= Y; i++) {
              let high = low - (this.boardWidth * i);
              if (this.board[high] !== 0) {
                const drop = this.dropGroup.children[high];
                const [x, y] = this.getCoordinatesOfDrop(X, Y);
                drop.tweener.moveTo(x, y, FALL_TIME).play();
                this._swapDrops(low, high);
                break;
              }
            }
          }
        }
      }
      // ドロップが落ちるのを待ってから、ドロップを消すメソッドを呼び出す
      this.tweener.wait(FALL_TIME + CALL_INTERVAL).call(this._clearDrops.bind(this)).play();
    },

    // 消えるドロップの座標を取得するメソッド
    // 戻り値：2次元配列(消える座標の配列が入った配列)、
    // または、空の配列（一つも消えないとき）
    _getClearablePlacesAsArray: function(_board) {
      let board = Array.from(_board),
          clearablePlaceArray = [],
          checked = new Set(),
          comboCount = 0;

      // ドロップが消える座標を取得
      const clearablePlaceSet = this._getClearablePlacesAsSet(board);

      // コンボごとに消える座標の配列を作る
      for (let Y = this.boardHeight; Y--;) {
        for (let X = 0; X < this.boardWidth; X++) {
          const index = this.get1dIndex(X, Y);
          if (clearablePlaceSet.has(index) && !checked.has(index)) {
            const color = board[index],
                  arrayToUse = clearablePlaceArray[comboCount] = [];
            this._pushClearablePlaces(board, index, color, clearablePlaceSet, arrayToUse, checked);
            comboCount++;
          }
        }
      }
      return clearablePlaceArray;
    },

    // 消えるドロップの座標を取得するメソッド
    // 戻り値：消える座標を値として持つSetインスタンス
    _getClearablePlacesAsSet: function(board) {
      const clearablePlaceSet = new Set();

      // 横に同じ色が３つ並んでいるかどうかを調べる
      for (let Y = 0; Y < this.boardHeight; Y++) {
        for (let X = 0; X < this.boardWidth - 2; X++) {
          const idx_1 = this.get1dIndex(X, Y),
                idx_2 = idx_1 + 1,
                idx_3 = idx_2 + 1;
          this._checkWhetherClearablePlaces(board, idx_1, idx_2, idx_3, clearablePlaceSet);
        }
      }
      // 縦に同じ色が３つ並んでいるかどうかを調べる
      for (let X = 0; X < this.boardWidth; X++) {
        for (let Y = 0; Y < this.boardHeight - 2; Y++) {
          const idx_1 = this.get1dIndex(X, Y),
                idx_2 = idx_1 + this.boardWidth,
                idx_3 = idx_2 + this.boardWidth;
          this._checkWhetherClearablePlaces(board, idx_1, idx_2, idx_3, clearablePlaceSet);
        }
      }
      return clearablePlaceSet;
    },

    // 与えられた配列について、与えられたIndexの要素がすべて等しく、かつ、
    // 数値型の０ではないときだけ、setに各Indexを追加するメソッド
    _checkWhetherClearablePlaces: function(array, idx_1, idx_2, idx_3, set) {
      if (array[idx_1] !== 0 &&
          array[idx_1] === array[idx_2] &&
          array[idx_2] === array[idx_3]) {
        set.add(idx_1);
        set.add(idx_2);
        set.add(idx_3);
      }
    },

    // board[index]のドロップ、及び、縦横で繋がっている同色のドロップの座標を、配列に格納するメソッド
    // コンボごとに座標をまとめるため、座標を格納することと、その座標を確認済みとする目的で使う
    _pushClearablePlaces: function(board, index, color, clearablePlaceSet, clearablePlaceArray, checked) {
      const directions = [
              {X: 0, Y: -1},  // 上への移動量
              {X: 0, Y: +1},  // 下への移動量
              {X: -1, Y: 0},  // 左への移動量
              {X: +1, Y: 0},  // 右への移動量
            ],
            [X, Y] = this.get2dIndex(index);

      clearablePlaceArray.push(index);  // この座標を配列に追加
      checked.add(index);               // この座標を調査済みとする

      // 上下左右を確認し、条件を満たす場合は、再帰呼び出しを行う
      for (let i = 0, len = directions.length; i < len; i++) {
        const nextX = X + directions[i].X,
              nextY = Y + directions[i].Y,
              nextIndex = this.get1dIndex(nextX, nextY);
        if (
          this.isInsideOfBoard(nextX, nextY) && // 次の座標が盤面の内側であるかどうか
          clearablePlaceSet.has(nextIndex) &&   // 次の座標のドロップが消えるかどうか
          board[nextIndex] === color &&         // 次の座標のドロップが同じ色かどうか
          !checked.has(nextIndex)               // 次の座標が非調査済みであるかどうか
        ){
          this._pushClearablePlaces(board, nextIndex, color, clearablePlaceSet, clearablePlaceArray, checked);
        }
      }
    },

    // 手順線を表示させるメソッド
    summonDragon: function(process) {
      this.process = process;
      this.dragon = Dragon(this.boardWidth, process, this.dropSize)
                    .moveTo(this.leftMargin, this.topMargin).addChildTo(this);
    },

    // 手順通りにドロップを自動で動かすメソッド
    // (ゲームを初期化してから実際のメソッドを呼び出す)
    // moveTime: １マスの移動にかける時間(ミリ秒)
    // moveDuration: メソッド呼び出しまでの待ち時間
    moveDrops: function(moveTime, moveDuration = 0) {
      if (!this.process) return;
      this.exit('main', {
        boardData: this.boardData,
        screenData: this.screenData,
        process: this.process,
        moveFlag: true,
        moveTime: moveTime,
        moveDuration: moveDuration,
      });
    },

    // 手順通りにドロップを自動で動かすメソッド
    // moveTime: １マスの移動にかける時間(ミリ秒)
    // count: 何回目の呼び出しであるか(デフォルト値があるので渡す必要はない)
    _moveDrops: function(moveTime, count = 0) {
      // 初回呼び出し時の処理
      if (count === 0) {
        const firstIndex = this.process[0];
        this.dropGroup.children[firstIndex].setAlpha(0.5);
        this.dragOkFlag = false;
      // 最終呼び出し時の処理
      } else if (count === this.process.length - 1) {
        const lastIndex = this.process.slice(-1)[0];
        this.dropGroup.children[lastIndex].setAlpha(1);
        this.dragOkFlag = true;
        this.changeFlag = true;
        return;
      }
      const currIndex = this.process[count],
            nextIndex = this.process[count + 1],
            currDrop = this.dropGroup.children[currIndex],
            nextDrop = this.dropGroup.children[nextIndex],
            [curr_x, curr_y] = [currDrop.x, currDrop.y],
            [next_x, next_y] = [nextDrop.x, nextDrop.y];

      currDrop.tweener.moveTo(next_x, next_y, moveTime).call(() => {
        this._swapDrops(currIndex, nextIndex);
        this._moveDrops(moveTime, count + 1);
      }).play();
      nextDrop.tweener.wait(moveTime / 2).call(() => {
        nextDrop.moveTo(curr_x, curr_y);
      }).play();
    },
  });
}