import * as phina from "phina.js"

// ベースとなるドロップクラス
//（必須引数：baseDropSize)
export const Drop = phina.define('Drop', {
  superClass: 'Sprite',
  init: function(baseDropSize) {
    this.superInit('drops', baseDropSize, baseDropSize);
  },

  // 透明度を変更するメソッド
  setAlpha: function(value) {
    this.alpha = value;
    return this;
  },
});

// 通常の移動可能なドロップクラス
// (必須引数：baseDropSize)
export const NormalDrop = phina.define('NormalDrop', {
  superClass: 'Drop',
  init: function(baseDropSize) {
    this.superInit(baseDropSize);
  },

  // (x, y)にdurationミリ秒かけて移動させるメソッド
  // 既にアニメーションが設定されている場合、先にその地点に即座に移動させる。
  tweenMoveTo: function(x, y, duration = 0) {
    if (this.destination) {
      this.tweener.clear();
      this.moveTo(...this.destination);
    }
    if (typeof(x) === 'number' && typeof(y) === 'number') {
      this.destination = [x, y];
      this.tweener.moveTo(x, y, duration).play()
        .call(() => delete this.destination);
    }
    return this;
  },
});

// ドロップの操作中に表示する半透明のドロップクラス
// (必須引数：baseDropSize)
export const GrabbedDrop = phina.define('GrabbedDrop', {
  superClass: 'Drop',
  init: function(baseDropSize) {
    this.superInit(baseDropSize);
  },

  // イベントの位置に応じて移動させるメソッド
  move: function(e) {
    const x = e.pointer.x,
          y = e.pointer.y - 5;
    this.moveTo(x, y);
    return this;
  },
});

// INPUTシーンの選択アイテムとしてのドロップクラス
// (必須引数：baseDropSize)
export const ItemDrop = phina.define('ItemDrop', {
  superClass: 'Drop',
  init: function(baseDropSize) {
    this.superInit(baseDropSize);
  },
  // アイテムを識別するための変数をセットするメソッド
  setItemIndex: function(index) {
    this._itemIndex = index;
    return this;
  },
  // 所属するシーンをセットするメソッド
  setScene: function(scene) {
    this._scene = scene;
    return this;
  },
  // 所属シーンのアイテムを選択するメソッドを呼び出す
  onpointstart: function() {
    this._scene.selectItem(this._itemIndex);
  },
});