<template>
  <div>
    <fieldset id="information">
      <div>
        <State :stateFlag=stateFlag />
        <SearchData
          :stateFlag=stateFlag
          :bestNode=bestNode
          :searchStatus=searchStatus
        />
      </div>
      <ComboData
        :comboData=comboData
        :maximum=maximum
        :dropTypesWithIndex=dropTypesWithIndex
      />
    </fieldset>
    <div id="information-bar">
      <div></div>
    </div>
  </div>
</template>

<script>
import State from './State.vue'
import SearchData from './SearchData.vue'
import ComboData from './ComboData.vue'

export default {
  name: 'Information',
  props: {
    searchStatus: String,
    stateFlag: Number,
    bestNode: Object,
    maximum: Object,
    dropTypesWithIndex: Array,
  },
  components: {
    State,
    SearchData,
    ComboData,
  },
  computed: {
    // 探索結果の最終盤面のコンボ情報
    comboData: function(){
      return this.bestNode ? this.bestNode.comboData : null;
    },
  },
}
</script>

