<template>
  <div>
    <div v-for="drop in drops" :key=drop.index :class="{many: drop[property1] >= 10}">
      <template v-if="drop[property2]">
        <img :src="drop.imgSrc"><!--
     --><span v-if="drop[property2] >= 2">{{ drop[property2] }}</span>
      </template>
    </div>
  </div>
</template>

<script>
export default {
  name: 'DropData',
  props: {
    drops: Array,
    property1: String,
    property2: String,
  },
}
</script>

<style scoped>
div {
  position: relative;
  width: 35px;
  height: 25px;
  margin-left: 5px;
  transition: 0.5s all;
}

div.many {
  width: 45px;
}

img {
  width: 25px;
}

div > span {
  position: absolute;
  bottom: 0;
  font-size: 0.9em;
  line-height: 0.9em;
}

.maximum {
  font-weight: bold;
}
</style>