<template>
  <input
    id="key-input"
    v-focus
    @keyup.left="$emit('capture')"
    @keyup.up="$emit('moveDrops')"
    @keyup.down="$emit('search')"
    @keyup.right="$emit('captureAndSearch')"
  />
</template>

<script>
export default {
  name: 'KeyInput',
  directives: {
    focus: {
      inserted: function (el) {
        el.focus();
      },
    },
  },
}
</script>

<style scoped>
#key-input {
  opacity: 0;
  position: absolute;
}
</style>
