<template>
  <fieldset id="state">
    <div>
      <p v-if="stateFlag === this.$STATE.STANDBY">STANDBY</p>
      <p v-else-if="stateFlag === this.$STATE.SEARCHING">SEARCHING...</p>
      <p v-else-if="stateFlag === this.$STATE.SEARCH_END">SEARCH END</p>
    </div>
  </fieldset>
</template>

<script>
export default {
  name: 'State',
  props: {
    stateFlag: Number,
  },
}
</script>

<style scoped>
#state {
  height: 75px;
}

#state > div {
  display: flex;
  justify-content: center;
  align-items: center;
}
</style>
