import * as phina from "phina.js";

// 必要
phina.globalize();

// 操作不可スプライトクラス
export default phina.define('ImmovablePosition', {
  superClass: 'Sprite',
  init: function(baseDropSize) {
    this.superInit('immovable', baseDropSize, baseDropSize);
  },
  setAlpha: function(alpha) {
    this.alpha = alpha;
    return this;
  },
  // アイテムを識別するための変数をセットするメソッド
  setItemIndex: function(index) {
    this._itemIndex = index;
    return this;
  },
  // 所属するシーンをセットするメソッド
  setScene: function(scene) {
    this._scene = scene;
    return this;
  },
  // 所属シーンのアイテムを選択するメソッドを呼び出す
  onpointstart: function() {
    this._scene.selectItem(this._itemIndex);
  },
});