<template>
  <canvas id="game-canvas"></canvas>
</template>

<script>
// ゲームを開始する関数(ゲームインスタンスをresolveするpromiseを返す)
import createGame from "../game/App.js"

export default {
  name: 'GameArea',
  props: {
    board: Array,
    boardSize: String,
    gameApp: Object,
  },
  computed: {
    // ゲームコンストラクタに渡す引数
    gameData: function(){
      return {
        board: this.board,
        boardSize: this.boardSize,
        vueComponent: this,
      };
    },
  },
  created: function() {
    // ゲームを初期化し、互いにインスタンスを持ち合う
    createGame(this.gameData)
    .then(app => this.$emit('input', app))  // eslint-disable-next-line
    .catch(err => console.error(err));
  },
  methods: {
    // ゲームインスタンスはこれらのメソッドを使用できる
    shuffle: function() {
      this.$emit('shuffle');
    },
    search: function() {
      this.$emit('search');
    },
  },
}
</script>

<style scoped>
#game-canvas {
  width: 375px;
  height: 667px;
  margin: 0;
  display: inline-block;
  vertical-align: bottom;
  cursor: pointer;
  border-radius: 3px;
  box-shadow: 0 -1px 8px #193648;
  border-radius: 25px 25px 5px 5px;
}
</style>
