<template>
  <fieldset id="combo-data">
    <table>
      <tr>
        <td>Max</td>
        <td class="combo">{{ maximum.combo }}combo</td>
        <td class="magni">×{{ maxMagnificationDisplay }}</td>
        <td @click="displayInfoFlag = !displayInfoFlag">
          <DropData
            v-if="displayInfoFlag"
            :drops=clearableDrops
            property1=maxCombo
            property2=maxComboCount
          />
          <DropData
            v-else
            :drops=existingDrops
            property1=count
            property2=count
          />
        </td>
      </tr>
      <tr>
        <template v-if="comboData"> 
          <td></td>
          <td class="combo" :class="maximumClass.combo">{{ comboData.combo }}combo</td>
          <td class="magni" :class="maximumClass.magni">×{{ magnificationDisplay }}</td>
          <td @click="displayInfoFlag = !displayInfoFlag">
            <DropData
              v-if="displayInfoFlag"
              :drops=clearableDrops
              property1=maxCombo
              property2=comboCount
            />
            <DropData
              v-else
              :drops=existingDrops
              property1=count
              property2=clearCount
            />
          </td>
        </template>
      </tr>
    </table>
  </fieldset>
</template>

<script>
import DropData from './DropData.vue'

export default {
  name: 'ComboData',
  props: {
    maximum: Object,
    comboData: Object,
    dropTypesWithIndex: Array,
  },
  components: {
    DropData,
  },
  data: function() {
    return {
      displayInfoFlag: true,  // 表示する情報（true:コンボ数 false:ドロップ数）        
    }
  },
  computed: {
    // 可能な最大倍率（表示用に四捨五入して３桁区切り）
    maxMagnificationDisplay: function() {
      return this.adjust(this.maximum.magnification);
    },
    // 探索結果の倍率（表示用に四捨五入して3桁区切り）
    magnificationDisplay: function() {
      return this.adjust(this.comboData.magnification);
    },
    // 存在するドロップの情報
    existingDrops: function() {
      return this.dropTypesWithIndex.filter(drop => drop.count);
    },
    // 消すことができるドロップの情報
    clearableDrops: function() {
      return this.dropTypesWithIndex.filter(drop => drop.maxComboCount);
    },
    // コンボ数や倍率が最大値の場合に、それぞれ「maximum」classが付与される
    maximumClass: function() {
      return {
        'combo': {'maximum': (this.maximum.combo === this.comboData.combo)},
        'magni': {'maximum': (this.maximum.magnification === this.comboData.magnification)},
      };
    },
  },
  methods: {
    // 数値を表示用に四捨五入して3桁区切りにするメソッド
    adjust: function(number) {
      if (number >= 100) {
        number = Math.round(number);
      }
      return number.toLocaleString();
    },
  }
}
</script>

<style scoped>
#combo-data {
  margin-top: 7px;
}

#combo-data table {
  height: 60px;
  border-collapse : collapse;
}

#combo-data td {
  height: 30px;
  line-height: 1em;
  padding: 0;
  vertical-align: bottom;
}

#combo-data .combo,
#combo-data .magni {
  text-align: right;
}

#combo-data tr:first-child .combo,
#combo-data tr:first-child .magni {
  padding-left: 7px;
}

#combo-data td:last-child > div {
  width: 260px;
  padding-left: 5px;
  cursor: pointer;
  display: flex;
  align-items: flex-end;
}
</style>
