<template>
  <div id="right-container">
    <KeyInput
      @capture="$emit('capture')"
      @moveDrops="$emit('moveDrops')"
      @search="$emit('search')"
      @captureAndSearch="$emit('captureAndSearch')"
    />
    <Information
      :searchStatus=searchStatus
      :stateFlag=stateFlag
      :bestNode=bestNode
      :maximum=maximum
      :dropTypesWithIndex=dropTypesWithIndex
    />
    <ControlPanel
      :maxCombo=maximum.combo
      :boardSettings=boardSettings
      :searchSettings=searchSettings
      :clearingSettings=clearingSettings
      @changeLeader1="$emit('changeLeader1', $event)"
      @changeLeader2="$emit('changeLeader2', $event)"
      @changeBoardSize="$emit('changeBoardSize', $event)"
    />
  </div>
</template>

<script>
import KeyInput from './KeyInput.vue'
import Information from './information/Information.vue'
import ControlPanel from './control_panel/ControlPanel.vue'

export default {
  name: 'RightContainer',
  props: {
    board: Array,
    maximum: Object,
    bestNode: Object,
    stateFlag: Number,
    searchStatus: String,
    boardSettings: Object,
    searchSettings: Object,
    clearingSettings: Object,
  },
  components: {
    KeyInput,
    Information,
    ControlPanel,
  },
  computed: {
    // 盤面に存在するドロップの数（種類別）
    dropCountArray: function() {
      const array = (new Array(this.$DROP_TYPE_MAX + 1)).fill(0);
      this.board.forEach(color => array[color]++);
      return array;
    },
    // 現在の盤面で可能なコンボ数（種類別）
    comboCountArray: function() {
      return this.dropCountArray.map(num => Math.floor(num / 3));
    },
    // ドロップの種類別にインデックスや個数などの情報を付与したオブジェクト配列
    dropTypesWithIndex: function() {
      return (new Array(this.$DROP_TYPE_MAX)).fill(0).map((e, i) => {
        const index = i + 1;
        return {
          index: index,
          imgSrc: require(`../assets/img/drop_${index}.png`),
          count: this.dropCountArray[index],
          maxComboCount: this.comboCountArray[index],
          clearCount: this.clearCountAsResult[index],
          comboCount: this.comboCountAsResult[index],
        };
      });
    },
    // 探索結果の動かし方により、消えるドロップの数(配列)
    clearCountAsResult: function() {
      if (this.bestNode) {
        return this.bestNode.comboData.cleared.map(array => 
          array.length ? array.reduce((a, b) => a + b) : 0
        );
      } else {
        return (new Array(this.$DROP_TYPE_MAX)).fill(0);
      }
    },
    // 探索結果の動かし方により、消えるコンボ数(配列)
    comboCountAsResult: function() {
      if (this.bestNode) {
        return this.bestNode.comboData.cleared.map(array => array.filter(n => n).length);
      } else {
        return (new Array(this.$DROP_TYPE_MAX)).fill(0);
      }
    },
  },
}
</script>

<style scoped>
#right-container {
  display: inline-block;
  margin-left: 10px;
}
</style>
