import * as phina from "phina.js"

const ASSETS = {
  image: {
    drops: require("../assets/img/drops.png"),
    tile_5x6: require("../assets/img/tile_5x6_3.png"),
    tile_6x7: require("../assets/img/tile_6x7_3.png"),
    immovable: require("../assets/img/immovable.png"),
    dragon_head: require("../assets/img/dragon_head_.png"),
    dragon_tail: require("../assets/img/dragon_tail_.png"),
  }
};
const SCENES = [
  {
    label: 'main',
    className: 'MainScene',
  },
  {
    label: 'input',
    className: 'InputScene',
    nextLabel: 'main',
  },
];
const SCREEN_PIXEL_HEIGHT = 1334; // ゲームキャンバス全体の高さ
const SCREEN_PIXEL_WIDTH = 750;   // ゲームキャンバス全体の幅
const BASE_DROP_SIZE = 53;        // 元となるドロップ画像のサイズ
// ドロップに適用するサイズの比率
const DROP_SCALE = {
  '5x6': 2.32,
  '6x7': 1.92,
}
// 盤面の余白（上部）
const TOP_MARGIN = {
  '5x6': 718,
  '6x7': 719,
};
// 盤面の余白（左側）
const LEFT_MARGIN = {
  '5x6': 5,
  '6x7': 17,
};
// 盤面の余白（右側）
const RIGHT_MARGIN = {
  '5x6': 6,
  '6x7': 19,
};

export default phina.define('MyGameApp', {
  superClass: 'phina.game.GameApp',
  
  init: function(options) {
    options = (options || {}).$safe({
      startLabel: 'main',
      assets: ASSETS,
      scenes: SCENES,
      width: SCREEN_PIXEL_WIDTH,
      height: SCREEN_PIXEL_HEIGHT,
      boardData: this._getBoardData(options.gameData),
      screenData: this._getScreenData(options.gameData),
      vueComponent: options.gameData.vueComponent,
    });
    this.superInit(options);
  },

  // 手順線を表示させるメソッド
  // process: 操作手順(座標の配列)
  displayLine: function(process) {
    const scene = this.currentScene;
    if ('summonDragon' in scene) {
      scene.summonDragon(process);
    }
    return this;
  },

  // 手順通りにドロップを動かすメソッド
  // moveTime: １マスあたりの移動にかける時間
  // duration: メソッド呼び出しまでの待機時間
  moveDrops: function(moveTime = 120, duration = 0) {
    const scene = this.currentScene;
    if ('moveDrops' in scene) {
      scene.moveDrops(moveTime, duration);
    }
    return this;
  },

  // 新しくゲームを開始するメソッド
  // boardData: 以下のプロパティを持つオブジェクト
  //   board: 盤面配列
  //   boardSize: 盤面のサイズ('5x6' or '6x7')
  //   startPosition: 開始位置指定(null or 0以上の整数値)
  //   immovablePositions: 操作不可位置(座標の配列)
  startNewGame: function(gameData) {
    const scene = this.currentScene;
    scene.exit('main', {
      boardData: this._getBoardData(gameData),
      screenData: this._getScreenData(gameData),
    });
    return this;
  },

  // 盤面に関する情報をオブジェクトにまとめるメソッド
  _getBoardData: function(gameData) {
    let { board, boardSize, startPosition, immovablePositions } = gameData;

    if (startPosition === undefined) {
      startPosition = -1;
    }
    if (immovablePositions === undefined) {
      immovablePositions = new Set();
    } else {
      immovablePositions = new Set(immovablePositions);
    }
    return {
      board: Array.from(board),
      boardSize: boardSize,
      startPosition: startPosition,
      immovablePositions: immovablePositions,
    }
  },

  // ゲーム作成に必要な情報をオブジェクトにまとめるメソッド
  _getScreenData: function(gameData) {
    const boardSize = gameData.boardSize;
    return {
      'screenPixelHeight': SCREEN_PIXEL_HEIGHT,
      'screenPixelWidth': SCREEN_PIXEL_WIDTH,
      'baseDropSize': BASE_DROP_SIZE,
      'dropScale': DROP_SCALE[boardSize],
      'topMargin': TOP_MARGIN[boardSize],
      'leftMargin': LEFT_MARGIN[boardSize],
      'rightMargin': RIGHT_MARGIN[boardSize],
    };
  },
})