<template>
  <label :class="{ checked: selected }">
    <input
      type="radio"
      v-show="false"
      :value="value"
      :checked="selected"
      @change="$emit('change', $event.target.value)"
    />
    {{ value }}
  </label>
</template>

<script>
export default {
  name: 'BoardSizeInput',
  props: {
    value: String,
    boardSize: String,
  },
  computed: {
    selected: function() {
      return this.value === this.boardSize;
    },
  },
}
</script>

<style scoped>
label {
  padding: 2px;
}

label:not(.checked) {
  cursor: pointer;
}
</style>