<template>
  <fieldset id="board-settings">
    <legend>BOARD SETTINGS</legend>
    <div>
      <label>BOARD<!--
     --><BoardSizeInput :value="'5x6'" :boardSize="boardSize" @change="$emit('changeBoardSize', $event)" />
        <BoardSizeInput :value="'6x7'" :boardSize="boardSize" @change="$emit('changeBoardSize', $event)" />
      </label>
      <label>LEADER<!--
     --><select :value=leader1 @change="$emit('changeLeader1', Number($event.target.value))">
          <option v-for="(label, index) in leaderLabels" :key=index :value="index">{{ label }}</option>
        </select>
        <select :value=leader2 @change="$emit('changeLeader2', Number($event.target.value))">
          <option v-for="(label, index) in leaderLabels" :key=index :value="index">{{ label }}</option>
        </select>
      </label>
    </div>
  </fieldset>
</template>

<script>
import BoardSizeInput from "./BoardSizeInput.vue"

export default {
  name: 'BoardSettings',
  props: {
    boardSettings: Object,
  },
  components: {
    BoardSizeInput,
  },
  data: function() {
    return {
      leaderLabels: ['METATRON', 'KOMA-S', 'AMEN', 'HYLEN', 'COCO', 'VEROAH', 'BARJULY'],
    }
  },
  computed: {
    boardSize: function() {
      return this.boardSettings.boardSize;
    },
    leader1: function() {
      return this.boardSettings.leader1;
    },
    leader2: function() {
      return this.boardSettings.leader2;
    },
  },
}
</script>

<style lang="scss" scoped>
#board-settings {
  margin-top: 10px;

  div {
    display: flex;
    align-items: center;

    > label {
      padding: 5px;
      font-size: 13px;
    }
    > label:nth-child(2) {
      margin-left: 30px;
    }

    select {
      background: rgb(7,11,23);
      color: orange;
      padding: 0 6px;
      cursor: pointer;
      border: none;
      text-align-last: center;
      -webkit-appearance: none;/* ベンダープレフィックス(Google Chrome、Safari用) */
      -moz-appearance: none;  /* ベンダープレフィックス(Firefox用) */
      appearance: none; /* 標準のスタイルを無効にする */ 
    }
    ::-ms-expand {  /* select要素のデザインを無効にする（IE用） */
      display: none;
    }
  }
}
</style>
